.mediabox-img.ls-blur-up-is-loading {
	visibility: hidden;
}
.mediabox-img.lazyload {
	&:not([src]) {
		visibility: hidden;
	}
}
.mediabox {
	position: relative;
	display: block;
	height: 0;
	width: 100%;
	padding-bottom: 66.6667%;
}
.ls-blur-up-img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: block;
	font-family: "blur-up: always", "object-fit: cover";
	object-fit: cover;
	filter: blur(10px);
	opacity: 1;
	transition: opacity 1000ms, filter 1500ms;
}
.mediabox-img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: block;
	font-family: "blur-up: always", "object-fit: cover";
	object-fit: cover;
}
.ls-blur-up-img.ls-inview.ls-original-loaded {
	opacity: 0;
	filter: blur(5px);
}
